.limit-message {
  color: red;
  font-weight: bold;
}

.increment-button, .decrement-button {
  padding: 10px 20px;
  margin: 5px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.increment-button:disabled, .decrement-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
